@import 'colors';

body {
    font: 400 15px Lato, sans-serif;
    line-height: 1.8;
    color: #818181;
}

body,html,.row-offcanvas {
  height:100%;
}


.nav, .pagination, .carousel, .panel-title a { cursor: pointer; }

.thumbnail:hover {
    cursor: pointer;
    box-shadow: 5px 0px 40px rgba(0,0,0, .2);
    color: $aqua;
}
.jumbotron { 
    background-color: $aqua;
    color: #ffffff;
    padding: 100px 25px;
    font: Montserrat, sans-serif;
}

.green{
    color: green;
}

.red{
    color: red;
}

.yellow{
    color: yellow;
}

.navbar {
    margin-bottom: 0;
    background-color: $orange;
    z-index: 9999;
    border: 0;
    font: Montserrat, sans-serif;
    font-size: 12px !important;
    line-height: 1.42857143 !important;
    letter-spacing: 4px;
    border-radius: 0;
}

.navbar li a, .navbar .navbar-brand {
    color: #fff !important;
}

.navbar-nav li a:hover, .navbar-nav li.active a {
    color: #f4511e !important;
    background-color: #fff !important;
}

.navbar-default .navbar-toggle {
    border-color: transparent;
    color: #fff !important;
}


.bg-grey {
    background-color: #f6f6f6;
}

.container-fluid {
    padding: 60px 50px;
}

.logo-small {
    color: #f4511e;
    font-size: 50px;
}

.logo {
    color: #f4511e;
    font-size: 200px;
}

#sidebar{
  width: inherit;
  min-width: 220px;
//   max-width: 220px;
//   background-color:#f5f5f5;
    background-color: $greytext;
  float: left;
  height:100%;
  position:relative;
  overflow-y:auto;
  overflow-x:hidden;

}

//app user panel stuff
.progress{
  max-width:1000px;
  margin: 40px auto;
